* {
  box-sizing: border-box;
}

h1, p {
  margin: 0;
}

img {
  display: block;
}

.is-hidden {
  visibility: hidden;
  pointer-events: none;
  position: absolute;
}

body {
  letter-spacing: .3px;
  font-size: 24px;
  line-height: 1.4;
}

.breed-select {
  border: 2px solid #db7093;
  border-radius: 10px;
  position: relative;
  left: 50%;
  transform: translate(-50%);
}

.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 5px solid #db7093;
  border-bottom-color: rgba(0, 0, 0, 0);
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.cat-info {
  justify-content: center;
  gap: 50px;
  margin-top: 50px;
  display: flex;
}

.cat-info__image {
  max-width: 800px;
  max-height: 600px;
  border-radius: 35px;
  overflow: hidden;
}

.cat-info__image > img {
  width: 100%;
  object-position: center;
  object-fit: cover;
}

.cat-info__text {
  max-width: 800px;
  max-height: 600px;
}

.Cat-info-title {
  margin-bottom: 16px;
  font-size: 36px;
}

.cat-info__description {
  margin-bottom: 16px;
}

